import React from "react";
import { TextField, Button } from "@mui/material";
import { useState } from "react";
import axios from "axios";

function Form() {
  const [data, setdata] = useState({ slug: "", url: "" });
  const submit = async () => {
    const result = await axios.post("/url", data);
    setdata({ slug: "", url: "" });
  };

  return (
    <div>
      <div>
        <TextField
          id="outlined-basic"
          label="slug"
          variant="outlined"
          value={data.slug}
          onChange={(e) => {
            setdata({ ...data, slug: e.target.value });
          }}
        />
      </div>
      <TextField
        id="outlined-basic"
        label="url"
        variant="outlined"
        value={data.url}
        onChange={(e) => {
          setdata({ ...data, url: e.target.value });
        }}
      />
      <div>
        <Button variant="contained" onClick={submit} onSubmit={submit}>
          Get shortened url
        </Button>
      </div>
    </div>
  );
}

export default Form;
